import React from "react";
import { Link } from "react-router-dom";
import CallApi from "../../api/api";
import { actionCreatorLogOut } from "./../../actions/actions";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";

class HeaderMenu extends React.Component {
  logOut = () => {
    this.props.closeMenu();
    const { session_id, onLogOut } = this.props;
    CallApi.delete("/authentication/session?", {
      body: { session_id: session_id }
    }).then(({ success = false }) => {
      if (success) {
        onLogOut();
      }
    });
  };

  render() {
    const {closeMenu} = this.props;
    return (
      <div className="dropdown-menu">
        <h6 className="dropdown-header"></h6>
        <div className="dropdown-divider" />
        <span className="dropdown-item" onClick={this.logOut}>
          
        </span>
        <span className="dropdown-item">
          <Link to={`${process.env.PUBLIC_URL}/account/favorites`} onClick={closeMenu}></Link>
        </span>
        <span className="dropdown-item">
          <Link to={`${process.env.PUBLIC_URL}/account/watchlist`} onClick={closeMenu}> </Link>
        </span>
      </div>
    );
  }
}

const mapStateToProps = store => {
  return {
    session_id: store.authentication.session_id
  };
};

const mapDispatchToProps = dispatch => {
  return bindActionCreators({
    onLogOut: actionCreatorLogOut
  }, dispatch)
 };

export default connect(mapStateToProps, mapDispatchToProps)(HeaderMenu);
